import React, { useContext } from 'react'
import Leaf from '../Leaf/Leaf'
import InterlinearLeaf from '../Leaf/InterlinearLeaf'
import SourceName from '../SourceName/SourceName'
import AppContext from '../../contextAPI/AppContext'

const NarratorText = (props) => {
    const receivedAppContext = useContext(AppContext)
    // const hebrew = props.language === 'HBR'
    return (
        <div>
            <SourceName sourceName={props.sourceData.source} />
            <div>
            {props.leaves.map(leaf => {
                if (receivedAppContext.interlinear) {
                    return <InterlinearLeaf 
                    marks={leaf.marks}
                    text={leaf.text}/>
                } else {
                    return <Leaf 
                    marks={leaf.marks}
                    text={leaf.text}/>
                }
            })}
            </div>
        </div>
)}

export default NarratorText;